.App {
  text-align: center;
}
.flex-container {
  display: flex;
  flex-direction: column;
  background-color: rgb(49, 47, 47);

}

.flex-item-0{
  order: 0;
  flex: 2;
}
.flex-item-1 {
  order: 1;
  flex: 2;
}


.flex-item-2 {
  order: 2;
  flex: 1;
  background-color: wheat;
}


.flex-item-0 img {

  height: 100%;
  width: 100%;
} 

.flex-item-1 img {

  height: 100%;
  width: 100%;
} 

.flex-item-2 img {

  height: 30%;
  width: 100%;
} 

.overlay{
  
  position: absolute;
  display: none;
  bottom: 0;
  left: 0;
  opacity: 0.8;
  height: 10%;
  width: 10%;
  background-color: rgb(54, 54, 54);
  

}

.flex-item-0:hover .overlay{
  display: block;
}

.flex-item-0:hover .overlay2{
  display: inline;
}

.overlay2{
  
  position: absolute;
  display: none;
  bottom: 0;
  left: 0;
  opacity: 0.8;
  height: 100%;
  width: 100%;
  background-color: rgb(54, 54, 54);
  animation: bounceIn 2s;

}

@keyframes bounceIn {

  0% {
    transform: scale(0.1);
    opacity: 0;
  }
  60% {
    transform: scale(0.7);
    opacity: 1;
  }
  100% {
    transform: scale(1);
  }
}